.modal {
  z-index: 999;
  width: 100%;
  height: 100%;
  max-height: 100%;
  background-color: rgba(0, 0, 0, .4);
  animation: fadeInOut .3s;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.modal.hide {
  animation: fadeOut .3s forwards;
}

.modal-content {
  width: 80vw;
  max-width: 900px;
  background-color: #fefefe;
  border: 1px solid #888;
  margin: 30px auto;
  padding: 30px 20px;
  position: relative;
}

